.jumbotron {
  margin: 0;
  background: url("/assets/img/jumbotronbg.jpg");
  background-size: cover;
  color: white;
  height: 100%
}

.jumbotron>.container>h1 {
  -webkit-text-stroke: 1px black;
}

.jumbotron>.container>h3 {
  font-size: 32px;
}

.jumbotron>.container>img {
  max-height: 300px;
}


